  [col-10] 3fr
  [col-11] 7fr
  [col-12] 7fr
  [col-13] 7fr
  [col-14] 7fr
  [col-end];

  grid-template-rows: 
  4fr
  5fr
  5fr
  5fr
  5fr
  5fr
  5fr 
  ;

  grid-template-areas: 
  ". logo logo . . . . . . . . . . ."
  "bw bw bw bw bw bw bw bw bw bw bw bw bw bw"
  "bw bw bw bw bw bw bw bw bw bw bw bw bw bw"
