  [col-5] 7fr
  [col-6] 7fr
  [col-7] 7fr
  [col-8] 7fr
  [col-9] 3fr
  [col-10] 3fr
  [col-11] 7fr
  [col-12] 7fr
  [col-13] 7fr
  [col-14] 7fr
  [col-end];

  grid-template-rows: 
  4fr
  5fr
  5fr
  5fr
  5fr
  5fr
  5fr 
