}

.white-block {
background-color: white;
grid-area:wb;
height: 100%;
width: 100%;
margin:0;
border-top-left-radius: 20px;
display: grid;

grid-template-rows:
1fr
24fr;

grid-template-columns:
[col-start] 1fr
[col-2] 1fr
[col-end];

grid-template-areas:
". ."
"textb gb"
"textb gb"
;
}

.green-block-landing {
background-color: #77D18C;
grid-area:gb;
margin:0;
border-top-left-radius: 20px;
padding-left:4%;
padding-top:2%;
min-width:80%;
}

.photo-block {
background-image: url("img/landingimage.jpg");
background-size:cover;
height:100%;
min-width:80%;
grid-area:pb;
margin:0;
border-top-left-radius: 20px;
background-color: grey;
}

.text-block {
  grid-area:textb;
  max-width:80%;
  display:flex;
  justify-content:center;
  align-items:flex-start;
  flex-direction:column;
  margin-left:15%;

}
h2 {
  font:42px Montserrat, sans-serif;
  font-weight:550;
  max-width:450px;
  margin:0;
  margin-bottom:8%;
  color:#255530
}

.logo {
  grid-area:logo;
  height:55px;
  margin:auto;
}



h4 {
  font:32px Montserrat, sans-serif;
  font-weight:500;
  color:#FFF4DA;
  border-bottom: 3px solid #FFF4DA;
  min-width:78%;
  margin-top:10%;
  margin-bottom:7%;
}

.landing-text { 
  font: 25px Montserrat, sans-serif;
  line-height:40px;
  max-width:450px;
  margin:0;
  color:#255530
}

.startbtn {
  color:#FFF4DA;
  background-color:#EF990A;
  border:none;
  width:100%;
  height:100%;
  font:18px Montserrat, sans-serif;
  font-weight:550;
  margin:auto;
  border-radius: 12px;
  cursor:pointer;
}

a {
  text-decoration: none;
}

.start-link {
  color:#FFF4DA;
  background-color:#EF990A;
  border:none;
  width:55%;
  height:9%;
  font:18px Montserrat, sans-serif;
  font-weight:550;
  margin-top:8%;
  border-radius: 12px;
  box-shadow: 0px 0px 14px 0px #dddddd;
}


