/*                                  GREENBLK.JS                          */

.green-block-main {
  grid-area:gbm;
  background-color: #77D18C;
  border-top-right-radius: 20px;
  display:grid;

  grid-template-columns:
  [col-start] 1fr
  [col-end];

  grid-template-rows:
  4fr
  1fr
  ;

  grid-template-areas:
  "guw"
  "rb"
}

.green-upper-wrapper{
  display:flex;
  flex-direction: column;
  align-items:center;
  grid-area:guw;
}

.search-bar{
  border:none;
  width:18.5vw;
  height:5vh;
  border-radius: 10px;
  margin:auto;
  font:18px Montserrat, sans-serif;
  color:#255530;
}

.auto-complete-dropdown{
  width:18.5vw;
  height:auto;
  background-color:white;
  border-radius: 10px;
  position:absolute;
  z-index:9;
}

.suggestions{
  font:18px Montserrat, sans-serif;
  color:#255530;
  padding-left:5px;
  cursor:pointer;
}

.suggestions:hover{
  font-weight: 450;
}

.green-info-wrapper{
  display:flex;
  flex-direction:column;
  align-items:center;
  margin-top:20px;
}

.green-instructions-wrapper{
  display:flex;
  flex-direction:column;
  align-items:center;
}

.ing-list{
  z-index:0;
  margin-top:20px;
}

.ing-wrapper{
  display: flex;
  min-width:18.5vw;
  justify-content: space-between;
  margin-bottom:12px;
}

.ingredient{
  min-width:80%;
  font:18px Montserrat, sans-serif;
  color:#FFF4DA;
  list-style-type: none;
  font-weight: 500;
}

.ing-x-button{
  border:none;
  background-color: transparent;
  color:#FFF4DA;
  font:18px Montserrat, sans-serif;
  font-weight: 600;
  cursor:pointer;
  border-radius:3px;
}

.ing-x-button:hover{
  background-color: #76ca89;
}

.green-arrow{
  height:40px;
  width:35px;
}

.green-instructions{
  font:20px Montserrat, sans-serif;
  font-weight:400;
  color:#FFF4DA;
  max-width:220px;
  margin-top:5%;
}

.reset-button{
  grid-area:rb;
  width: 18.5vw;
  height: 5vh;
  margin:auto;
  background-color:#EDF9F0;
  border:none;
  border-radius:10px;
  font:18px Montserrat, sans-serif;
  font-weight:550;
  color:#255530;
  cursor:pointer;
}

.reset-button:hover{
  background-color:#f9fffb;
}





