/*                               RESULTS.JS                         */

.white-block-main {
  grid-area:wbm;
  background-color:white;
  border-top-left-radius: 20px;
  display:flex;
  overflow:auto;
  flex-wrap:wrap;
  justify-content:space-around;
}

.placeholder-plate{
  margin:auto;
  height:40vh;
}



.recipes-wrapper{
  display:flex;
  flex-wrap:wrap;
  justify-content:space-around;
}


.recipe-card{
  margin:20px;
  border-radius:25px;
  overflow:hidden;
  max-width:min-content;
  min-height:max-content;
  display:flex;
  flex-direction:column;
  text-decoration: none;
  margin-bottom:10px;
}

.recipe-card-title-bar{
  min-height:10%;
  background-color:white;
  padding:auto;
  display:flex;
  justify-content:center;
}

.recipe-card-icon-bar{
  background-color:#EDF9F0;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  display:flex;
  justify-content:space-evenly;
}
  .card-text{
  color:#255530;
  max-height:min-content;
  max-width: 80%;
  text-align:center;
  margin-top:12px;
  margin-bottom:12px;
  line-height:10px;
  font: 20px Montserrat, sans-serif;
  font-weight:500;
}

.card-icon-text{
  color:#255530;
  max-height:min-content;
  max-width: 80%;
  text-align:center;
  line-height:10px;
  font: 18px Montserrat, sans-serif;
  font-weight:300;
  margin-left:8px;
  margin-top:12px;
  margin-bottom:12px;
}

.time-display{
  display:flex;
  align-items: center;
  justify-content: space-around;
}

.clock-logo{
  height:22px;
}

.diet-logo{
  height:25px;
}





/*                                  FILTERS.JS                         */

.filter-wrapper{
  display:flex;
  height: 100%;
  max-height:9vh;
  grid-area: fbtn;
  align-items:center;
}

.time-wrapper{
  display:flex;
  flex-direction:column;
  height:50%;
  overflow:visible;
}

.time-button{
  display:flex;
}
.time-light{
  background-color:#EF990A;
  width:12vw;
  height:4.58vh;
  border-top-left-radius:50px;
  border-bottom-left-radius:50px;
  display:flex;
  align-items:center;
  justify-content:center;
}

.time-dark{
  background-color:#DC8D09;
  min-width:2.5vw;
  height:4.58vh;
  border-top-right-radius:50px;
  border-bottom-right-radius:50px;
  display:flex;
  align-items:center;
  justify-content:center;
  cursor:pointer;
}

.time-dropdown{
  z-index:1;
  display:flex;
  flex-direction:column;
  position:relative;
  top:4px;
  display:none;
  background-color:#DC8D09;
  border-radius:15px;
  border-bottom-left-radius:15px;
  border-bottom-right-radius:15px;

}
.time-option{
  z-index: 1;
  background-color:transparent;
  font:17px Montserrat, sans-serif;
  color:#FFF4DA;
  border:none;
  cursor:pointer;
}

.time-option:hover{
font-weight:480;
}

.filter-arrow{
  height:10px;
}

.diet-wrapper{
  display:flex;
  height:50%;
}

.diet-light{
  background-color:#EF990A;
  margin-left:20px;
  width:8vw;
  height:4.58vh;
  border-top-left-radius:50px;
  border-bottom-left-radius:50px;
  display:flex;
  align-items:center;
  justify-content:center;
}

.diet-dark{
  background-color:#DC8D09;
  display:flex;
  min-width: 20vw;
  height:4.58vh;
  border-top-right-radius:50px;
  border-bottom-right-radius:50px;
  align-items:center;
  justify-content:space-evenly;
}

.gluten-btn{
  height:15px;
  width:15px;
  background-color:transparent;
  border:#FFF4DA solid 2px;
  border-radius:5px;
  cursor:pointer;
}

.veg-btn{
  height:15px;
  width:15px;
  background-color:transparent;
  border:#FFF4DA solid 2px;
  border-radius:5px;
  cursor:pointer;

}
.filter-text{
  font:20px Montserrat, sans-serif;
  color:#FFF4DA;

}

.filter-subtext{
  margin:0;
  font:17px Montserrat, sans-serif;
  color:#FFF4DA;
  margin-left:7px;
}





