.rp-white{
  display:flex;
  flex-direction:row;
  height:100%;
  width:100%;
  border-top-left-radius: 20px;
  grid-area:w;
  background-color:white;
  align-items:center;
  justify-content:space-around;
}

.recipe-display{
  margin:20px;
  border-radius:25px;
  overflow:hidden;
  max-width:min-content;
  min-width:420px;
  display:flex;
  flex-direction:column;
  text-decoration: none;
}

.recipe-details{
  display:flex;
  flex-direction:column;
}

.ingredients-list-wrapper{
  display:grid;
  grid-template-columns:
  [col-start] 1fr
  [col-2] 1fr
  [col-end];
}

.instructions-list-wrapper{
