}

.instructions-list{
    color:#255530;
    max-height:min-content;
    max-width: 80%;
    margin-top:12px;
    margin-bottom:12px;
    line-height:10px;
    font: 14px Montserrat, sans-serif;
    font-weight:500;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
